.checkbox{-webkit-appearance:none;appearance:none;position:relative;display:flex;width:16px;height:16px;margin-right:8px;border:1px solid rgba(22,24,35,.12);cursor:pointer}.checkbox:checked::before{content:"✓";font-size:1.6rem;line-height:1.6rem;color:#fff;position:absolute;right:15%;bottom:-1px}.checkbox:checked{background-color:var(--primary);border-color:var(--primary);position:relative}.label-checkbox{display:flex;align-items:center}.label-checkbox.checkRight{flex-direction:row-reverse;justify-content:flex-end}.label-checkbox span{font-size:1.6rem;font-weight:500;margin-right:12px}