.wrapper {
    display: flex;
    flex-direction: column;
    max-height: min((100vh - 96px) - 60px, 734px);
    min-height: 100px;
    // min-width: 223px;
    min-width: 180px;
    max-width: var(--search-box-width);
    width: 100%;
    background: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 12px;
    border-radius: 8px;
    padding-top: 8px;
}
