.preview {
    margin-right: 8px;

    .preview-player {
        position: relative;
        background-color: transparent;
        line-height: 0;
        width: 284px;
        height: 587px;
        z-index: 1;
    }
    .change-video-btn {
        margin-top: 5px;
        background: var(--white);
        border-radius: 12px;
        padding: 12px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &.card {
            border: 1px solid rgba(22, 24, 35, 0.12);
            margin-top: 16px;
        }
    }
}

.preview-player {
    .app-frame {
        background-image: url(data:image/png;base64,);
        background-position: 50%;
        background-size: cover;
        height: 100%;
        width: 100%;
        min-width: 100%;
        min-height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: -1;
    }
    .app-tab {
        position: absolute;
        top: 529px;
        left: 0px;
        right: 0px;
        margin: auto;
        width: 259px;
        height: 34px;
        background-size: cover;

        &.fyp-tab {
            background-image: url(data:image/png;base64,);
        }
    }
    .feed-page {
        width: 100%;
        height: 100%;
        opacity: 0;
        pointer-events: none;
        &.show {
            opacity: 1;
            pointer-events: unset;
        }
    }
    &:hover {
        .controls-container {
            opacity: 1;
        }
    }
}

.preview .change-video-btn {
    .file {
        font-weight: 400;
        font-size: 1.2rem;
        line-height: 17px;
        color: rgba(22, 24, 35, 0.75);
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 150px;

        .file-icon {
            margin-right: 4px;
        }

        .file-name {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
        }
    }

    .change-btn {
        font-size: 1.2rem;
        font-weight: 500;
        padding: 0;
        cursor: pointer;
        appearance: none;
        background-color: transparent;
        &:hover {
            text-decoration: underline;
        }
    }
}

.feed-page .video-player {
    width: 100%;
    height: 100%;

    .video-container {
        background: rgb(0, 0, 0);
        position: absolute;
        left: 12.5px;
        right: 12.5px;
        top: 10.5px;
        height: 518.5px;
        border-top-left-radius: 27.5px;
        border-top-right-radius: 27.5px;
        overflow: hidden;
    }
    .video-overlay {
        .head {
            display: flex;
            align-items: center;
            position: absolute;
            top: 40px;
            left: 0;
            right: 0;
            margin: 0 20px;
            opacity: 0.6;

            .header-text {
                flex-grow: 1;
                font-size: 1.2rem;
                font-weight: 700;
                text-align: center;
            }
            .search-icon {
                color: var(--white);
            }
        }
    }
    .video-controls {
        position: absolute;
        inset: 0px;
        z-index: 3;
    }
}

.header-text {
    span {
        margin: 0 10px;
        &:last-child::after {
            width: 18px;
            height: 2px;
            position: absolute;
            content: '';
            left: 65%;
            bottom: -8px;
            border-radius: 2px;
            transform: translateX(-50%);
            background-color: currentColor;
        }
    }
}

.video-overlay .sessions-right {
    position: absolute;
    right: 20px;
    bottom: 12%;
    display: flex;
    flex-direction: column;

    .avatar {
        background-color: #fff;
        border-radius: 50%;
        height: 40px;
        width: 40px;
    }
    .actions-group {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        color: var(--white);
        opacity: 0.6;
        margin-bottom: 20px;
        margin: 20px 0;
        height: 125px;
    }
    .record {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: #fff;
    }
}

.video-overlay .sessions-left {
    position: absolute;
    left: 0;
    bottom: 60px;
    color: var(--white);
    opacity: 0.6;
    font-size: 1.4rem;
    padding-left: 20px;

    .username,
    .description,
    .music {
        width: 190px;
        margin-bottom: 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .music {
        display: flex;
        align-items: center;
        .music-icon {
            margin-right: 8px;
            z-index: 1;
        }
    }
}

.video-controls .controls-container {
    position: absolute;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 0px 0px 35px 35px;
    height: 90px;
    padding: 0px 22.5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    opacity: 0;
    transition: opacity 200ms ease 0s;
    background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.85) 100%);

    .controls-operator {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .play-info {
            display: flex;
            align-items: center;
        }
        .operations-btn {
            display: flex;
            align-items: center;
            gap: 16px;
        }
    }

    .progress-bar-container {
        height: 3px;
        background: rgba(86, 86, 86, 0.8);

        .progress-bar {
            --seek-before-width: 0;
            appearance: none;
            height: 100%;
            background: rgb(255, 255, 255);
            position: relative;
            width: var(--seek-before-width);
            transition: 0.2s linear;
            &::after {
                content: '';
                display: block;
                background: rgb(255, 255, 255);
                box-shadow: rgba(0, 0, 0, 0.1) -1px 1px 1px;
                border-radius: 50%;
                width: 5px;
                height: 5px;
                position: absolute;
                top: 50%;
                right: 0px;
                transform: translate(50%, -50%);
                z-index: 2;
            }
            &::-webkit-slider-thumb {
                -webkit-appearance: none;
                appearance: none;
                background-color: #fff;
                opacity: 0;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                cursor: pointer;
            }
        }
    }
}

.controls-operator .play-info {
    .play-btn {
        width: 16px;
        height: 16px;
        background-size: 16px 16px;
        cursor: pointer;
    }
    .play-time {
        font-weight: 600;
        font-size: 1.4rem;
        line-height: 1em;
        padding: 0px 6px;
        color: var(--white);
    }
}

.controls-operator .operations-btn {
    .volume-btn,
    .full-screen-btn {
        width: 16px;
        height: 16px;
        background-size: 16px 16px;
        cursor: pointer;
    }
    .volume-btn {
    }
    .full-screen-btn {
        background-image: url(data:image/svg+xml;base64,);
    }
}

// Change video modal
.modal-wrapper {
    .modal-title-container {
        padding: 32px 32px 24px;
        .modal-title {
            font-weight: 600;
            font-size: 2rem;
            text-align: center;
        }
        .modal-subtitle {
            font-weight: 400;
            font-size: 1.5rem;
            line-height: 18px;
            opacity: 0.5;
            margin-top: 8px;
        }
    }
    .modal-btn {
        padding: 16.5px 16px 17px;
        text-align: center;
        font-weight: 400;
        font-size: 1.5rem;
        line-height: 18px;
        border-top: 1px solid rgba(22, 24, 35, 0.12);
        cursor: pointer;
        &.emphasis {
            color: var(--primary);
            font-weight: 600;
        }
    }
}
